.select {
  position: relative;
  display: block;
  min-width: 220px;
  max-width: 233px;
}

.select__head {
  width: 100%;
  max-width: 100%;
  border: 2px solid rgba(51, 51, 51, 0.54);
  border-radius: 10px;
  padding: 10px 40px 10px 30px;
  cursor: pointer;
  text-align: center;
  min-width: 250px;
  color: #fff;
}
.select__head span {
  color: #FE0000;
}

.select__head::after {
  width: 10px;
  height: 6px;
  background: #000 url("../img/select-icon.svg") no-repeat center/cover;
  position: absolute;
  right: 10px;
  bottom: 50%;
  transform: translateY(50%);
  content: "";
  display: block;
  transition: 0.2s ease-in;
}

.select__head.open::after {
